import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 
      -apple-system,BlinkMacSystemFont,
      "Segoe UI",
      "Roboto", 
      sans-serif;
  }
`;

GlobalStyle.displayName = "GlobalStyles";
